class Body extends React.Component {
  constructor() {
    super(...arguments);
    this.state = { opened: false, image: "", name: "", description: "" };
  }

  open = () => {
    this.setState({ opened: true });
  };

  close = () => {
    this.setState({ opened: false });
  };

  storeDetail = id => {
    this.props.stores.map(store => {
      if (store.id === id) {
        this.setState({
          name: store.name,
          image: store.image,
          description: store.description
        });
      }
    });
  };

  render() {
    const { image, name, description } = this.state;
    const { stores } = this.props;
    return (
      <React.Fragment>
        <h2 className="store-list-title">Store List</h2>
        <ul className="container">
          {stores.map(store => (
            <li onClick={this.open} key={store.id} className="item">
              <img
                src={store.thumb}
                onClick={() => this.storeDetail(store.id)}
                alt={`${store.name} image`}
              />
            </li>
          ))}
        </ul>
        {this.state.opened && (
          <Portal selector="#modal">
            <div className="overlay">
              <div className="modal">
                <img src={image} alt="store image" className="modal-image" />
                <h3 className="modal-store-name">{name}</h3>
                <p className="modal-store-description">{description}</p>

                <button type="button" onClick={this.close}>
                  Close Modal
                </button>
              </div>
              <style jsx global>{`
                body {
                  overflow: hidden;
                }
              `}</style>
              <style jsx>{`
                .overlay {
                  position: fixed;
                  background-color: rgba(0, 0, 0, 0.7);
                  top: 0;
                  right: 0;
                  bottom: 0;
                  left: 0;
                }

                .modal {
                  background-color: white;
                  position: absolute;
                  top: 10%;
                  right: 10%;
                  bottom: 10%;
                  left: 10%;
                  padding: 1em;
                }

                .modal-store-name {
                  margin-top: 1.5rem;
                  font-size: 2rem;
                  color: #e67e22;
                }

                .modal-store-description {
                  margin-top: 10px;
                  line-height: 1.5rem;
                }
              `}</style>
            </div>
          </Portal>
        )}
      </React.Fragment>
    );
  }
}

export default Body;
